import Layout from "./pages/layout";
import { useState, useEffect } from "react";
import { Box, useMediaQuery } from "@mui/material";
import {Home,Login,Profile, Register} from './pages';
import ModeSwitcher from "./components/darkMode/switcher";
import DrawerSwiper from "./components/drawer/swiper";
import DrawerContent from "./components/menu/menu";
import {
  OtherHousesRounded,
  PersonRounded,
  SettingsRounded,
  ExitToAppRounded,
} from '@mui/icons-material';

import {
  hslaPicker,
} from "./components/mode";

const handleWindowResize = () => {
  return(
    ((window.innerWidth <= 764) && 'mobile') ||
    ((window.innerWidth >= 1080) && 'desktop') ||
    ((window.innerWidth < 1080 && window.innerWidth > 764) && 'tablet')
  );
};


function App() {
  
  const [menu, setMenu]=useState([
    {
      icon: <OtherHousesRounded fontSize='inherit' color="inherit"/>,
      func: ()=>{setDrawer(false); setPage('home')}
    },
    {
      icon: <PersonRounded fontSize='inherit' color="inherit"/>,
      func: ()=>{setDrawer(false); setPage('profile')}
    },
    {
      icon: <SettingsRounded fontSize='inherit' color="inherit"/>,
      func: ()=>{setDrawer(false); setPage('login')}
    },
    {
      icon: <ExitToAppRounded fontSize='inherit' color="inherit"/>,
      func: ()=>{setDrawer(false); setPage('register');}
    },
  ]);

  const [user, setUser]=useState({});
  const [drawer, setDrawer]=useState(false);
  const [page, setPage]=useState('home');
  const [pageOption , setPageOption]=useState(null);
  const [theme, setTheme] = useState({});
  const [screen, setScreen] = useState( handleWindowResize() );
  const [mode, setMode]= useState( useMediaQuery('(prefers-color-scheme: dark)') ? 'dark': 'light' );

  const [touchPosition, setTouchPosition] = useState(null)

  const handleTouchStart = (e) => {
    const touchDown = e.touches[0].clientX
    setTouchPosition(touchDown)
  }

  const handleTouchMove = (e) => {
    const touchDown = touchPosition
    if(touchDown === null) return;
    const currentTouch = e.touches[0].clientX
    const diff = touchDown - currentTouch
    if (diff < 50) setDrawer(true);
    if (diff > -50) setDrawer(false);
    setTouchPosition(null)
  }

  useEffect(() => {
    window.addEventListener('resize', ()=>{setScreen(handleWindowResize())});
    return () => window.removeEventListener('resize', ()=>{setScreen(handleWindowResize())});
  },[]);

  return (
    <Layout theme={theme} mode={mode}>
      <ModeSwitcher mode={mode} setMode={setMode}
        sx={{ position: 'fixed', top: '0px', left: '0px', zIndex: 200 }}
      />
      <DrawerSwiper drawer={drawer} setDrawer={setDrawer}>
        <DrawerContent menu={menu} mode={mode} setDrawer={setDrawer}>
          {pageOption}
        </DrawerContent>
      </DrawerSwiper>
      <Box position='relative' component='main' overflow='auto' width='100%' height='100%'
        onTouchMove={handleTouchMove}
        onTouchStart={handleTouchStart}
        onClick={()=>{setDrawer(false)}}
        sx={{
          backdropFilter:'blur(5px)',
          backgroundColor: hslaPicker({h:222,s:100,l:98},{h:0,s:0,l:16}),
        }}
      >
        {
          (page==='home' && <Home screen={screen} setPage={setPage} setPageOption={setPageOption} />) ||
          (page==='login' && <Login screen={screen} setPage={setPage} setPageOption={setPageOption} />) ||
          (page==='register' && <Register screen={screen} setPage={setPage} setPageOption={setPageOption} />) ||
          (page==='profile' && <Profile mode={mode} screen={screen} page={page} setPage={setPage} setMenu={setMenu} setPageOption={setPageOption} setDrawer={setDrawer}/>)
        }
      </Box>
    </Layout>
  )
}

export default App;
