import { IconButton, Stack } from "@mui/material"
import { Card } from 'ui-neumorphism'
import './neu.css'

function DrawerContent({screen, setDrawer ,menu=[], pageOption, children, mode}) {
    return (
        <Stack direction='row'
          sx={{
            overflowX:'hidden',
            overflowY:'auto',
            height: (screen==='mobile') ? '70vh' : '80vh',
            alignItems: 'stretch',
            justifyContent:'center',
            backgroundColor: (theme)=>{
              return (theme.palette.mode==='dark') ? 'rgba(10,10,10,.8)' : 'rgba(255,255,255,.8)'
            }
          }}
        >
          <Stack gap={2}
            sx={{
              px: '15px',
              justifyContent: 'center',
              alignItems:'center',
              fontSize: '2rem',
              height:'100%',
              color: `hsl(${7000}, 60%, 35%)`
            }}
          >
          {
            menu.map(({icon, func},index)=>{
              return(
                <IconButton color="inherit" onClick={func} key={index}>
                  {icon}
                </IconButton>
              )
            })
          }
          </Stack>
          
          {(pageOption || children) && 
           (
            
              <Card inset rounded dark={mode==='dark' ? true : false}
                style={{
                  display:'flex',flexDirection:'column', gap:'1rem',
                  justifyContent:'center', alignItems:'center',
                  margin: '14px 15px 14px 0', padding:'10px 45px', 
                  backgroundColor:'hsla(175, 44%, 45%,.2)',
                  overflow:'auto',
                }}
              >
                {pageOption}
                {children}
              </Card>
            
            
           )
          }
    
        </Stack>
      )
}

export default DrawerContent