function Profile({screen,setPageOption,mode,setDrawer}) {

  const [time, setTime] = useState(new Date(new Date().setHours(8,44,59,995)));
  const [instrument, setInstrument]=useState();
  const [order, setOrder]=useState('buy');
  const [price, setPrice]=useState();
  const [asset, setAsset]=useState();
  const [quantity, setQuantity]=useState();
  const [modal, setModal] = useState(false);

  useEffect(()=>{
    setPageOption(
      <>
        <AnalogClock sx={{width:'155px'}} hslColor={7000}/>
        <PickDate sx={{width: '155px'}} time={time} setTime={setTime}/>
        <PickTime sx={{width: '95px'}} time={time} setTime={setTime}/>
        <AddJob size={'3rem'} onClick={()=>{setDrawer(false); setModal(true)}} />
      </>
    );
      
  },[time]);

  return (
    <>
      {!modal ? 
        null :
        <Modal setModal={setModal} setDrawer={setDrawer}>
          <JobForm mode={mode} time={time}
            order={order} setOrder={setOrder}
            price={price} setPrice={setPrice}
            asset={asset} setAsset={setAsset}
            quantity={quantity} setQuantity={setQuantity}
            instrument={instrument} setInstrument={setInstrument}
          />
        </Modal>
      }

      <Box
        mx='1rem'
        height='100%'
        overflow='hidden'
      >
        <Card rounded dark={mode==='dark' ? true : false}
          style={{
            padding:'20px',
            borderRadius:'1rem',
            top:'40px',
            position:'sticky',
            zIndex:2,
            fontFamily:'persian-M', direction: 'rtl',
            backdropFilter:'blur(5px)',
            backgroundColor:'inherit'
          }}
        >
          <Typography fontFamily='persian-M' dark={mode==='dark' ? true : false}>
            
          </Typography>
        </Card>
        <Grid spacing={2} container 
          sx={{
            py:'20px',
            height: '85%',
            position:'relative',
            top:'60px',
            width:'100%',
            borderRadius:'1rem',
            mx:'auto',
            overflow:'auto',
            alignItems:'center',
            justifyContent:'center',
          }}
        >
        {
          jobs.map((job,index)=>{
            return (
              <Grid item key={index}>
                <JobCard job={job} mode={mode}/>
              </Grid>
            )
          })
        }
        </Grid>
      </Box>
    </>
  )
}

export default Profile