import Input from '@mui/material/Input';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';


export default function InputField({icon, type='text', label, value, onChange, error}) {

  return(
    <FormControl variant="standard" error={error} sx={{m:'10px 5px 5px 5px',display:'flex'}}>
        <InputLabel>{label}</InputLabel>
        <Input
          type={type}
          endAdornment={
            <InputAdornment position="end">
              {icon}
            </InputAdornment>
          }
          label={label}
          value={value}
          defaultValue={value}
          onChange={onChange}
        />
    </FormControl>
  )
}