import { Box, colors } from '@mui/material';
import React, { useState } from 'react';
import './analog.css';

const AnalogClock = ({sx , hslColor=0}) => {
  const [hour, setHour] = useState('');
  const [minutes, setMinutes] = useState('');
  const [seconds, setSeconds] = useState('');

  const clock = () => {
    let date = new Date();
    let hh = date.getHours() * 30;
    let mm = date.getMinutes() * 6;
    let ss = date.getSeconds() * 6;

    setHour(`rotateZ(${hh + mm / 12}deg)`);
    setMinutes(`rotateZ(${mm}deg)`);
    setSeconds(`rotateZ(${ss}deg)`);
  };
  setInterval(clock, 200);
  
  return (
    <Box sx={{'--hue-color': hslColor, ...sx}} className="clock-circle">
      <Box component='span' className="clock-twelve"></Box>
      <Box component='span'className="clock-three"></Box>
      <Box component='span' className="clock-six"></Box>
      <Box component='span' className="clock-nine"></Box>

      <Box className="clock-rounder"></Box>
      <Box className="clock-hour" style={{ transform: hour }}></Box>
      <Box className="clock-minutes" style={{ transform: minutes }}></Box>
      <Box className="clock-seconds" style={{ transform: seconds }}></Box>
    </Box>
  );
};

export default AnalogClock;