import { ButtonGroup, Button, Box } from "@mui/material"
import { useEffect } from "react"

function Home({screen, setPage ,setPageOption}) {
  useEffect(()=>{
    setPageOption(null)
  },[]);
  
  return (
    <Box
      sx={{
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}
    >
      <ButtonGroup
        size="large"
        variant="text"
        orientation={(screen==='mobile'? 'vertical' : 'horizontal')}
      >
        <Button onClick={()=>setPage('login')}>login</Button>
        <Button onClick={()=>setPage('register')}>register</Button>
      </ButtonGroup>
    </Box>
  )
}

export default Home