function modePicker({light,dark}) {
    return (theme) =>{
        return (theme.palette.mode === 'light') ? light : dark
    }
}

function hslaPicker(lightColor={}, darkColor={}) {

    lightColor = {h:0,s:0,l:100,a:1 , ...darkColor, ...lightColor}
    darkColor = {h:0,s:0,l:10,a:1 , ...lightColor, ...darkColor}
    return (theme) =>{
      return (theme.palette.mode === 'light') ? 
        `hsla(${lightColor.h}, ${lightColor.s}%, ${lightColor.l}%, ${lightColor.a || 1})`:
        `hsla(${darkColor.h}, ${darkColor.s}%, ${darkColor.l}%, ${darkColor.a || 1})`
    }
}

export  {
    modePicker,
    hslaPicker,
}