import dayjs from 'dayjs';
import AdapterJalaali from '@date-io/jalaali';
import TextField from '@mui/material/TextField';
import { MobileTimePicker } from '@mui/x-date-pickers/MobileTimePicker';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { Stack } from '@mui/material';

function checkMs (msec){
    msec = msec.replace(/[^0-9]/ig,'');
    return (msec) ? msec.match(/([0-9]{1,3})/)[1] : '';
}
function PickTime({sx, time, setTime, disable=false}) {

  return (
    <LocalizationProvider dateAdapter={AdapterJalaali}>
        <Stack direction='row'>
            <MobileTimePicker
                disabled={disable}
                ampm={false}
                openTo="hours"
                views={['hours', 'minutes', 'seconds']}
                inputFormat="HH:mm:ss"
                mask="__:__:__"
                renderInput={(params) => <TextField sx={{...sx}} {...params} />}
                value={time.getTime()}
                label="Select Time"
                onChange={(newValue) => {
                    newValue = newValue.toDate();
                    setTime(new Date(
                        time.
                        setUTCHours(
                            newValue.getUTCHours(),
                            newValue.getUTCMinutes(),
                            newValue.getUTCSeconds(),
                            newValue.getUTCMilliseconds()
                        )
                    ));
                }}
                minTime={dayjs().set('hour',8).set('minute', 30).set('second',0).set('millisecond',0)}
                maxTime={dayjs().set('hour',12).set('minute', 30).set('second',0).set('millisecond',0)}
            />

            <TextField
                disabled={disable}
                value={time.getMilliseconds()}
                type='text'
                onChange={(event)=>{
                    setTime( new Date( time.setMilliseconds(+checkMs(event.target.value)) )) 
                }}
                label="ms" variant="outlined"
                sx={{...sx, ml:'5px', width:'55px'}}
            />
        </Stack>
        
        
    </LocalizationProvider>

  )
}

export default PickTime