import {
    Box,
    Chip,
    Alert,
    Typography,
    IconButton,
    ButtonGroup,
} from '@mui/material';
import { Card } from 'ui-neumorphism'

import {
    SellOutlined,
    CloseOutlined,
    LocalAtmOutlined,
    SupportAgentRounded,
    ModeEditOutlineRounded,
    PendingActionsOutlined,
    AddShoppingCartOutlined,
    ProductionQuantityLimitsOutlined,
} from '@mui/icons-material';


function removeJobCard(job){

}
function modifyJobCard(job){

}

function showInfo(timeline){

}

function JobCard({job:{job, timeline=[]},lang='Fa', mode}) {
  return (
    <Card elevation={2} rounded dark={mode==='dark' ? true : false}
        style={{
            overflow: 'hidden',
            position: 'relative',
            width: '160px',
            backgroundColor: (job.error && 'rgba(239, 83, 80,.7)') || (job.success && 'rgba(38, 166, 154,.7)') || 'rgba(250, 250, 250,.5)',
            backdropFilter: 'blur(8px)'
            
        }}
          
    >
        <ButtonGroup sx={{position: 'absolute', right: '5px'}}
        >
            {
                (job.error || job.success) ? null :
                <IconButton onClick={()=>{modifyJobCard(job)}} sx={{p:0}}>
                    <ModeEditOutlineRounded />
                </IconButton>
            }
            <IconButton onClick={()=>{removeJobCard(job)}}>
                <CloseOutlined />
            </IconButton>
        </ButtonGroup>
        
        <Box sx={{py:.75, px:2 , display: 'flex', flexDirection:'column'}}>
            
            <Typography gap={0.5} display='flex' flexDirection='row' alignItems='center' variant='overline'>
                {
                (job.order === 'sell') ?
                <SellOutlined /> : 
                <AddShoppingCartOutlined />
                }
                {
                (job.order === 'sell') ? 'Sell' : 'Buy'
                }
            </Typography>

            <Box sx={{mt:.5, direction: (lang==='Fa') ? 'rtl' : 'inherit' }} fontSize='.7em' fontFamily='persian-M'>        
                <Typography gap={0.5} display='flex' flexDirection='row' alignItems='center' fontSize='1rem' fontFamily='inherit' component='span'>
                    <PendingActionsOutlined fontSize='inherit' />
                    {job.instrument}
                </Typography>

                <Typography gap={0.5} display='flex' flexDirection='row' alignItems='center' fontSize='inherit' component='span' fontFamily='inherit'>
                    <LocalAtmOutlined fontSize='inherit'/>
                    {job.price + `${
                        (isNaN(job.price) && '*') ||
                        ( (lang==='Fa') ? ' ' : ' Rial')
                    }`}
                </Typography>

                <Typography gap={0.5} display='flex' flexDirection='row' alignItems='center' fontSize='inherit' component='span' fontFamily='inherit'>
                    <ProductionQuantityLimitsOutlined fontSize='inherit'/>
                    {job.quantity}
                </Typography>


                <Typography mt={.5} gap={0.5} display='flex' flexDirection='row' alignItems='center' fontSize='inherit' component='div' fontFamily='inherit'>
                    <SupportAgentRounded />
                    <Chip onClick={()=>{ window.location.href=`${job.urlBroker}`;}} clickable label={job.broker} sx={{fontFamily:'inherit', fontSize:'inherit', maxWidth:'80%'}}/> 
                </Typography>
            </Box>
        </Box>
        <Alert
            severity={(job.success && 'success') || (job.error && 'error') || ('info')}
            sx={{py:0,fontSize:'.7rem', display: 'flex', flexDirection:'row', alignItems:'center'}}
            onClick={()=>showInfo(timeline)}
        >
            {timeline[timeline.length-1].title}       
        </Alert>
    </Card>
  )
}

export default JobCard