import { useEffect, useState } from 'react';
import {
    Card,
    TextField,
    Typography,
    Button,
    Tabs,Tab,TabItems,TabItem,
    } from 'ui-neumorphism'
import AlarmOnRoundedIcon from '@mui/icons-material/AlarmOnRounded';
import EventAvailableRoundedIcon from '@mui/icons-material/EventAvailableRounded';
import AddShoppingCartRoundedIcon from '@mui/icons-material/AddShoppingCartRounded';
import {
    SellOutlined,
    RequestQuoteOutlined,
    PlaylistAddOutlined,
    AccountBalanceWalletOutlined,
} from '@mui/icons-material';

import InputField from '../inputs/standard';
import { Box } from '@mui/material';

function JobForm({time=new Date(), mode,
    order, setOrder,
    price, setPrice,
    asset, setAsset,
    quantity, setQuantity,
    instrument, setInstrument
}) {
    const [active, setActive]=useState(0);

    useEffect(()=>{
        setOrder(active ? 'sell' : 'buy');
    },[active]);

  return (
    <Card rounded
        dark={mode==='dark' ? true:false}
        style={{
            overflowX:'hidden',
            overflowY:'auto',
            maxHeight:'80vh',
            maxWidth:'80vw',padding:'20px',
            display:'flex',flexWrap:'wrap',
            margin:'auto',
            justifyContent:'center',alignItems:'center',
            backgroundColor: (mode === 'dark') ? 'rgba(10,10,10,0.8)' : 'rgba(240,240,240,0.8)',
        }}
    >
        <Box sx={{flexShrink: .5, display:'flex', flexDirection:'column',gap:1.5, alignItems:'center'}}>
            <Typography type='h6' dark={mode==='dark' ? true:false}>
                New Order
            </Typography>

            <TextField dark={mode==='dark'?true:false}
                type='text'
                bordered
                readonly={true}
                inputStyles={{width:'100%'}}
                label= {
                    time.getFullYear()+'-'+
                    (+time.getMonth()+1)+'-'+
                    time.getDate()
                }
                hideExtra={true}
                hint='Inserted Date'
                append={<EventAvailableRoundedIcon />}
            />

            <TextField dark={mode==='dark' ? true:false}
                type='text'
                bordered
                readonly={true}
                inputStyles={{width:'100%'}}
                label= {
                    time.getHours()+':'+
                    time.getMinutes()+':'+
                    time.getSeconds()+'.'+
                    time.getMilliseconds()
                }
                hint='Inserted Time'
                append={<AlarmOnRoundedIcon />}
            />
        </Box>
        <Box sx={{
            m: '10px',
            display:'flex',
            flexDirection:'column',
            justifyContent:'center',
            alignItems:'center',
            minWidth: '250px',
            maxWidth: '100%',flexGrow: 1,
            }}
        >
            <Card rounded dark={mode==='dark' ? true:false}
                style={{
                    margin:'10px 0px',width:'100%', padding:'20px 15px',
                    backgroundColor: 'transparent'
                }}
            >
                <Tabs rounded value={active}
                    onChange={({active}) => {console.log(active); setActive(active);}}
                >
                    <Tab style={{width:'50%'}}>{<AddShoppingCartRoundedIcon />}&nbsp; buy</Tab>
                    <Tab style={{width:'50%'}}>{<SellOutlined />}&nbsp; sell </Tab>
                </Tabs>
                <TabItems value={active} style={{width: '90%',margin:'10px auto',}}>
                    <TabItem>
                        <InputField
                            label='Price'
                            value={price}
                            onChange={(e)=>{setPrice(e.target.value)}}
                            icon={<RequestQuoteOutlined color={'inherit'||'success'} />}
                        />
                        {(asset) ? null :
                            <InputField
                                label='Quantity'
                                value={quantity}
                                onChange={(e)=>{setQuantity(e.target.value)}}
                                icon={<AccountBalanceWalletOutlined color={'warning' || 'error'} />}
                            />
                        }
                        {(quantity) ? null :
                            <InputField
                                label='Asset'
                                value={asset}
                                onChange={(e)=>{setAsset(e.target.value)}}
                                icon={<PlaylistAddOutlined color={'error'}/>}
                            />
                        }
                    </TabItem>
                    <TabItem>
                        <InputField
                            label='Price'
                            value={price}
                            onChange={(e)=>{setPrice(e.target.value)}}
                            icon={<RequestQuoteOutlined color={'inherit'||'success'} />}
                        />
                        {(asset) ? null :
                            <InputField
                                label='Quantity'
                                value={quantity}
                                onChange={(e)=>{setQuantity(e.target.value)}}
                                icon={<AccountBalanceWalletOutlined color={'warning' || 'error'} />}
                            />
                        }
                        {(quantity) ? null :
                            <InputField
                                label='Asset'
                                value={asset}
                                onChange={(e)=>{setAsset(e.target.value)}}
                                icon={<PlaylistAddOutlined color={'error'}/>}
                            />
                        }
                    </TabItem>
                </TabItems>
            </Card>

            <Button dark={mode==='dark' ? true:false} rounded
                bgColor={'inherit'}
                style={{width:'50%',marginTop:'10px'}}
            >
                Send
            </Button>
        </Box>

    </Card>   
  )
}

export default JobForm