import { Box, Stack } from '@mui/material';
import ChevronRightRounded from '@mui/icons-material/ChevronRightRounded';


function DrawerSwiper({drawer, setDrawer,children}) {
    return (
        <Box
            sx={{
                position: 'fixed', top: 0, left:0, zIndex: 100,
                display:'flex', flexDirection:'column',
                justifyContent:'center',
                alignItems:'center',
                height:'100%',
                //maskImage:
                //    `linear-gradient(90deg, rgba(255,255,255,0.9) 20%, rgba(0,255,186,0) 90%),
                //    linear-gradient(180deg, rgba(255,255,255,0.9) 20%, rgba(0,255,186,0) 90%)`,
            }}
        >
            <Stack position='relative' direction='row' alignItems='center' >
                <Box sx={{
                        maxWidth: (drawer) ? '100%' : '0px',
                        backdropFilter: (drawer) ? 'blur(20px)' :'inherit',
                        transition: '0.5s',
                        overflow:'hidden',
                        borderRadius:'0 1.8rem 1.8rem 0',
                        borderRight: (theme)=>{
                            return (theme.palette.mode==='dark') ? '2px solid white' : '2px solid black'
                        },
                    }}
                >
                    {children}
                </Box>
                <Box
                    sx={{
                        display:'flex',
                        backdropFilter: 'blur(5px)',
                        borderRadius:'0 10rem 10rem 0',
                        boxShadow: '-10px 0px 10px 0px rgba(0,0,0,.3)',
                        backgroundColor: (theme)=>{
                            return (theme.palette.mode==='dark') ? 'rgba(255,255,255,.8)' : 'rgba(0,0,0,.8)' 
                        }
                    }}
                >
                    <ChevronRightRounded
                        onClick={()=>{setDrawer(drawer?false:true)}}
                        sx={{
                            height:'10rem',
                            width:'1.2rem',
                            cursor:'pointer',
                            transform: (drawer) ? 'rotate(180deg)' : 'rotate(0deg)', transition: '.4s',
                            color: (theme)=>{
                                return (theme.palette.mode==='dark') ? '#000000' : '#ffffff' 
                            }
                        }}
                    />
                </Box>
                
            </Stack>
        </Box>
    )
}

export default DrawerSwiper